body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.landing{
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(135deg , #031F8F 5%, #000000 30% ,black 60%);
}
.container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 50px 0;
  height: fit-content;
}
.header{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
.header>*{
  margin-top: 10px;
}
.header>h2{
  color: #B7B7B7;
  font-size: 20px;
  height: 40%;
}
.img{
  background-image: url('./components/img/me.png');
  width: 100px;
  height: 100px;
  border-radius: 25%;
  background-position: center;
  background-size: cover;
  background-color: rgb(165, 77, 77);
}

.work{
  color: white;
  
  margin: 50px 0 0 -100px;
  font-size: 20px !important;
}
.content{
  margin-top: 50px !important;
  width: 80%;
  margin: auto;
  background-color: rgba(100, 148, 237, 0);
  height: 200px;
}
.row{
  justify-content: center;
}
.content img{
  width: 100px;
  height: 100px;
  box-shadow: 0 0 11px white;
  border-radius: 10px;
}
.content h4{
  margin-top: 5px;
  color: white;
}
.content h6{
  color: #AFAFAF;
}
.item{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
.footer{
  width: 100%;
  height: 30vh;
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
}
.links{
  display: flex;
  justify-content: space-around;
  width: 300px;
}
.link:hover,.item:hover{
  transform: scale(1.1);
  transition: transform .2s ease-out;
}
